.App {
  overflow: hidden; }
  .App .AppBar {
    text-align: center;
    width: 100%;
    /* margin-left: 25%; */ }
  .App .map-content {
    margin-left: 25%; }
  .App .pad2 {
    padding: 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; }
  .App .toast-content {
    text-align: left;
    font-size: 0.8em; }
    .App .toast-content h3 {
      margin-top: 0;
      padding-top: 0; }
  .App .toastify-content--success {
    background: #135D13; }
  .App .text-center {
    text-align: center; }
  .App .text-right {
    text-align: right; }
  .App .margin-top-sm {
    margin-top: 5px !important; }
  .App .margin-top-md {
    margin-top: 10px !important; }
  .App .margin-top-lg {
    margin-top: 20px !important; }
  .App .margin-bottom-sm {
    margin-bottom: 5px !important; }
  .App .margin-bottom-md {
    margin-bottom: 10px !important; }
  .App .margin-bottom-lg {
    margin-bottom: 20px !important; }
  .App .margin-all-md {
    margin: 10px !important; }
