.settings .settings__open-btn {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 1001; }

.settings .settings__drawer {
  padding: 15px 10px;
  overflow: hidden !important; }
  .settings .settings__drawer .settings__actions {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .settings .settings__drawer .settings__actions h1 {
      font-family: 'Century Gothic', 'Apple Gothic', AppleGothic, 'URW Gothic L', 'Avant Garde', Futura, sans-serif;
      margin: 0;
      line-height: 0.8; }
    .settings .settings__drawer .settings__actions .settings__close-btn {
      margin: 0 0 10px 0; }
  .settings .settings__drawer .settings__filters {
    margin-bottom: 30px; }
    .settings .settings__drawer .settings__filters .settings__filter-btns {
      margin-top: 35px;
      text-align: center; }
  .settings .settings__drawer .events__list h3 {
    text-align: center;
    margin: 0 0 10px 0;
    padding: 0; }
  .settings .settings__drawer .grid__event {
    position: relative;
    margin: 5px; }
    .settings .settings__drawer .grid__event:hover {
      cursor: pointer;
      box-shadow: 0px 0px 10px #0097a7; }
    .settings .settings__drawer .grid__event.selected {
      box-shadow: 0px 0px 10px #0097a7; }
    .settings .settings__drawer .grid__event .grid__event-downvote {
      position: absolute !important;
      right: 0;
      bottom: 10px;
      z-index: 1001; }
    .settings .settings__drawer .grid__event.grid__event-selected {
      box-shadow: 0px 0px 10px #ff4081; }
    .settings .settings__drawer .grid__event .grid__event-header-bar {
      position: absolute;
      right: 0px;
      left: 0px;
      bottom: 0px;
      background: rgba(0, 0, 0, 0.4);
      display: flex;
      align-items: center;
      height: 68px; }
      .settings .settings__drawer .grid__event .grid__event-header-bar .grid__event-header {
        flex-grow: 1;
        margin-left: 16px;
        margin-right: 0px;
        color: white;
        overflow: hidden; }
        .settings .settings__drawer .grid__event .grid__event-header-bar .grid__event-header .grid__event-header-title {
          font-size: 16px;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          display: block; }
        .settings .settings__drawer .grid__event .grid__event-header-bar .grid__event-header .grid__event-header-subtitle {
          font-size: 12px;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          display: block; }
      .settings .settings__drawer .grid__event .grid__event-header-bar .grid__event-header-icon {
        order: 1; }
  .settings .settings__drawer .settings__loading {
    text-align: center; }

.settings__help h4 {
  margin: 20px 0 0 0;
  padding: 0; }

.settings__help .settings__help_list {
  display: flex;
  justify-content: space-between; }
  .settings__help .settings__help_list dt {
    font-weight: bold; }
  .settings__help .settings__help_list dd {
    margin: 0 0 15px 0; }
