.events .events__open-btn {
  position: absolute;
  top: 50%;
  right: 10px;
  z-index: 1001; }

.events .events__drawer .events__video_player {
  text-align: center;
  align-content: center; }

.events .events__drawer .events__video_player h4 {
  margin: 0 0 3px 0; }

.events .events__drawer .events__video_player video {
  width: 100%;
  height: auto; }

.events .events__drawer .events__event_header {
  width: 95%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: baseline;
  margin: -20px auto 10px auto; }
  .events .events__drawer .events__event_header .events__vote_controls {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .events .events__drawer .events__event_header .events__vote_controls span:first-of-type {
      margin-right: 15px; }
  .events .events__drawer .events__event_header .events__event_title {
    text-align: center; }
    .events .events__drawer .events__event_header .events__event_title h2 {
      margin: 0;
      padding: 0;
      font-size: 16px; }
    .events .events__drawer .events__event_header .events__event_title h4 {
      margin: 4px 0 0 0;
      padding: 0;
      font-size: 14px; }

.events .events__drawer .events__action-div {
  text-align: center;
  display: flex;
  justify-content: space-between; }
  .events .events__drawer .events__action-div .events__close-evt-btn {
    margin: 10px; }
  .events .events__drawer .events__action-div .events__add-pt-btn {
    display: inline-block; }
  .events .events__drawer .events__action-div .events__close-btn {
    margin: 10px; }

.events .events__drawer #event-start, .events .events__drawer #event-end, .events .events__drawer #event-time {
  font-size: 0.8em !important; }
